import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from './core/auth.guard';
import { UserLoginComponent } from './ui/user-login/user-login.component';
import { HomePageComponent } from './ui/home-page/home-page.component';
import { NotesListComponent } from './notes/notes-list/notes-list.component';
import { UploadPageComponent } from './uploads/upload-page/upload-page.component';
import { UserListComponent } from './users/user-list/user-list.component';
import { MachinesListComponent } from './machines/machines-list/machines-list.component';
import { MonitoringListComponent } from './monitoring/monitoring-list/monitoring-list.component';
import { EnComponent } from './presentation/en/en.component';
import { FiComponent } from './presentation/fi/fi.component';
import { AboutPageComponent } from './ui/about-page/about-page.component';
import { ContactPageComponent } from './ui/contact-page/contact-page.component';

import { SsrPageComponent } from './ui/ssr-page/ssr-page.component';


const routes: Routes = [
  { path: '', component: HomePageComponent },
  { path: 'login', component: UserLoginComponent },
  { path: 'contact', component: ContactPageComponent },
  { path: 'about', component: AboutPageComponent },
  { path: 'notes', component: NotesListComponent,  canActivate: [AuthGuard] },
  { path: 'uploads',  component: UploadPageComponent,  canActivate: [AuthGuard] },
  { path: 'users',  component: UserListComponent,  canActivate: [AuthGuard] },
  { path: 'machines',  component: MachinesListComponent,  canActivate: [AuthGuard] },
  { path: 'monitoring',  component: MonitoringListComponent,  canActivate: [AuthGuard] },
  { path: 'presentation/en',  component: EnComponent,  canActivate: [AuthGuard] },
  { path: 'presentation/fi',  component: FiComponent,  canActivate: [AuthGuard] },
  { path: 'ssr', component: SsrPageComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
