import { Injectable } from '@angular/core';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';


@Injectable()

export class MachinesService {

  machinesCollection: AngularFirestoreCollection<any>;
  machineDocument:   AngularFirestoreDocument<any>;

  constructor(private afs: AngularFirestore) {

    this.machinesCollection = this.afs.collection('machines', (ref) => ref.orderBy('name', 'desc'));
   }

   getData(): Observable<any[]> {
    // ['added', 'modified', 'removed']
    return this.machinesCollection.snapshotChanges().pipe(
      map((actions) => {
        return actions.map((a) => {
          const data = a.payload.doc.data();
          return { id: a.payload.doc.id, ...data };
        });
      })
    );
  }


  getMachine(id: string) {
    return this.afs.doc<any>(`machines/${id}`);
  }

  createMachine(name: string, ip:string, enabled: boolean) {
    const machine = {
      name,
      ip,
      enabled,
    };
    return this.machinesCollection.add(machine);
  }

  updateMachine(id: string, data: any) {
    return this.getMachine(id).update(data);
  }

  deleteMachine(id: string) {
    return this.getMachine(id).delete();
  }

}
