import { Injectable } from '@angular/core';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';


@Injectable()

export class UsersService {

  usersCollection: AngularFirestoreCollection<any>;
  userDocument:   AngularFirestoreDocument<any>;


  constructor(private afs: AngularFirestore) { 
    this.usersCollection = this.afs.collection('users', (ref) => ref.orderBy('email', 'desc'));

  }

  getData(): Observable<any[]> {
    // ['added', 'modified', 'removed']
    return this.usersCollection.snapshotChanges().pipe(
      map((actions) => {
        return actions.map((a) => {
          const data = a.payload.doc.data();
          return { id: a.payload.doc.id, ...data };
        });
      })
    );
  }

  getUser(uid: string) {
    return this.afs.doc<any>(`users/${uid}`);
  }

  createUser(displayName: string, email: string) {
    const user = {
      displayName,
      email,
      isAdmin: false,
    };
    return this.usersCollection.add(user);
  }

  updateUser(uid: string, data: any) {
    return this.getUser(uid).update(data);
  }

  deleteUser(uid: string) {
    return this.getUser(uid).delete();
  }

}
