import { Component, OnInit } from '@angular/core';
import { MachinesService } from '../machines.service';
import { Observable } from 'rxjs';
import { AuthService } from '../../core/auth.service';

@Component({
  selector: 'machines-list',
  templateUrl: './machines-list.component.html',
  styleUrls: ['./machines-list.component.scss']
})
export class MachinesListComponent implements OnInit {

  machines: Observable<any[]>;
  name: string;
  ip: string;
  enabled: boolean=true;

  constructor(private machinesService: MachinesService, public auth: AuthService) { }


  ngOnInit() {
    this.machines = this.machinesService.getData();
  }

  clickHandler() {
    this.machinesService.createMachine(this.name, this.ip, this.enabled);
    this.name = '';
    this.ip = '';
  }

}
