import { Component, OnInit } from '@angular/core';
import { MachinesService } from '../../machines/machines.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'monitoring-list',
  templateUrl: './monitoring-list.component.html',
  styleUrls: ['./monitoring-list.component.scss']
})
export class MonitoringListComponent implements OnInit {

  machines: Observable<any[]>;
  selectedmachine: any = "";

  constructor(private machinesService: MachinesService) { 
    
  }

  ngOnInit() {
    this.machines = this.machinesService.getData();  
  }

  showMachine(machi:any){
    this.selectedmachine=machi;
    //console.log(machi.id);
  }

}
